import {useSignFn} from "../../../utils/sign_fn";
import {useWallet} from "use-wallet";
import {useCallback} from "react";
import {HttpServer} from "../../../net/http_server";
import {AppConfig} from "../../../config";
import BigNumber from "bignumber.js";


export const useClaimPay = (claim_id: string, ) => {
    const sign = useSignFn();
    const {account} = useWallet();
    const fatch = useCallback(async (pay: BigNumber)=>{
        let _params = claimPayAppleJson(claim_id, pay.toString(),account);
        let _hex = await sign(_params);
        let _data = HttpServer.claimPay(_hex,_params);
        return _data;
    },[claim_id,account]);
    return fatch;
}

const claimPayAppleJson = (claim_id: string, pay: string,account:string|null): string => {
    let method = "claim_pay";

    return JSON.stringify({
        domain: {
            name: method,
            chainId: AppConfig.chainId.toString(),
        },
        message: {
            method: method,
            content: {
                user_id: account,
                claim_id: parseInt(claim_id),
                pay,
            }
        },
        primaryType: 'Place',
        types: {
            EIP712Domain: [
                {name: 'name', type: 'string'},
                {name: 'chainId', type: 'uint256'},
            ],
            Place: [
                {name: 'content', type: 'Model'},
                {name: 'method', type: 'string'},
            ],
            Model: [
                {name: 'user_id', type: 'string'},
                {name: 'claim_id', type: 'uint256'},
                {name: 'pay', type: 'string'},
            ],

        }
    });
}
